import {Component, ViewChild, ViewContainerRef} from '@angular/core';
import {FieldWrapper} from '@ngx-formly/core';

export interface PngFieldSetWrapperProps {
  fieldSet: {
    legend?: string;
    toggleable?: boolean;
    styleClass?: any;
    style?: any
  }
}

@Component({
  template: `
    <p-fieldset [collapsed]="to['fieldSet']?.collapsed"
                [legend]="to['fieldSet']?.legend"
                [styleClass]="to['fieldSet']?.styleClass"
                [style]="to['fieldSet']?.style"
                [toggleable]="to['fieldSet']?.toggleable || true">
      <ng-container #fieldComponent></ng-container>
    </p-fieldset>
  `,
  styleUrls: ['styles.scss']
})
export class PngFieldSetWrapperComponent extends FieldWrapper {
  @ViewChild('fieldComponent', {read: ViewContainerRef}) fieldComponent: ViewContainerRef;
}
