import {Component, ViewChild, ViewContainerRef} from '@angular/core';
import {FieldWrapper} from '@ngx-formly/core';

export interface PngCardWrapperProps {
  card: {
    icon?: string;
    header?: string;
    styleClass?: any;
    style?: any;
  };
}

@Component({
  template: `
    <div class="card" [ngClass]="to['card']?.styleClass" [ngStyle]="to['card']?.style">
      <h3 class="card-header"><i [ngClass]="to['card']?.icon"></i> <span [innerHTML]="to['card']?.header"></span></h3>
      <div class="card-body">
        <ng-container #fieldComponent></ng-container>
      </div>
    </div>
  `
})
export class PngCardWrapperComponent extends FieldWrapper {
  @ViewChild('fieldComponent', {read: ViewContainerRef}) fieldComponent: ViewContainerRef;
}
