import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {FormGroup} from '@angular/forms';
import {DynaContentComponent} from '@er/core';
import {DynaContent} from '@er/types';
import {Subscription} from 'rxjs';
import {distinctUntilChanged} from 'rxjs/operators';
import {PngFormlyFieldType} from '../../base/field-type';

export interface DynaProps {
  content: DynaContent;
  refreshOnFieldChanges: string;
  refreshOnFormChanges: boolean;
}

@Component({
  template: `
    <er-dyna-content [content]="props['content']" [context]="{field: field}"></er-dyna-content>
  `
})
export class PngFormlyFieldDynaComponent extends PngFormlyFieldType implements OnInit, OnDestroy {

  @ViewChild(DynaContentComponent) comp: DynaContentComponent;

  subscription: Subscription;

  ngOnInit() {
    super.ngOnInit();
    if (this.props['refreshOnForm']) {
      const form: FormGroup = this.field.formlyForm.form;
      this.subscription = form.valueChanges
        .pipe(distinctUntilChanged())
        .subscribe(_ => {
          this.comp.refresh();
        });
    } else if (this.props['refreshOnField']) {
      const formControl = this.field.formlyForm.formControl(this.props['refreshOnField']);
      this.subscription = formControl.valueChanges
        .pipe(distinctUntilChanged())
        .subscribe(_ => {
          this.comp.refresh();
        });
    } else {
      this.comp.refresh();
    }
  }

  unsubscribe() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  ngOnDestroy() {
    this.unsubscribe();
  }
}
