import {Component, OnInit} from '@angular/core';
import {PngFormlyFieldType} from '../../base/field-type';

@Component({
  template: `
    <png-input
      formlyPropsBind
      [attrs]="attrs"
      [formControl]="formControl"
    >
    </png-input>
  `
})
export class PngFormlyInputComponent extends PngFormlyFieldType implements OnInit {

  ngOnInit() {
    super.ngOnInit();
    this.field['attrs'] = {
      ...this.to || {},
      ...this.attrs
    };
    if (!this.field['attrs']['disabled']) {
      delete this.field['attrs']['disabled'];
    }
  }
}
